.store{
    position: relative;
    //height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.store div{
    //margin: 10px 0;
    //height: 20vh
}
.btn{
    display: flex;
    justify-content: center;
    color: black;
    font-size: 20px;
    margin: 10px 0;
    background: transparent;
    opacity: 0.9;
    padding: 5px 20px;
    border: 1px solid black;
    border-radius: 5px;
    &:hover{
        font-weight: bold;
    }
}

.StorePreviewBox{
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70vh;
    flex-wrap: wrap;
    background-color: white;
    width: 100%;
    .storedivBox{
        width: 100%;
        display: grid;
        gap: 15px;
        grid-template-rows: 100% 100% 100% 100%;
        grid-template-columns: 25% 25% 25% 25%;
    }
}
@media (max-width: 992px){
    .StorePreviewBox {
        height: 110vh;
        .storedivBox {
            grid-template-columns: 45% 45%;
            grid-template-rows: 50% 50% 50% 50%;
        }
    }
}

@media (max-width: 414px) {

    .StorePreviewBox {
        height: 100vh;
        .storedivBox {
            grid-template-columns:  100%;
            grid-template-rows: 50% 25%;
            overflow: hidden;
            &:nth-child(3) {
                display: none;
                visibility: hidden;
            }
            &:nth-child(6){
                display: none;
                visibility: hidden;
            }
        }



    }
}