.Preview{
    line-height: 29px;
    width: 60%;
    font-weight: 400;
    font-size: 18px;
    color: var(--clr-heading);
    line-height: 29px;
    margin: 10px auto;
}
.PreviewDiv{
    font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
    width: 100%;
    text-align: center;
    padding: 55px 0;
    justify-content: center;
    zoom: 1;
    display: inline-block;
    vertical-align: middle;
    border-top: 1px solid #bababa;
    border-bottom: 1px solid #bababa;
}
.h2{
    line-height: 33px;
    font-size: 28px;
    color: var(--clr-heading);
    margin: 10px 0;
    font-weight: bold;
}
