.header{
    font-family: Rufina, serif;
    padding-top: 3vh;
    padding-bottom: 3vh;
    display: flex;
    justify-content: center;
}
.navLeft,
.navRight{
    align-items: center;
    display: flex;
}
.navRight{
    padding-left: 3vw
}
.navLeft{
    padding-right: 3vw
}
.a{

}

#divA{
    margin-left: 0.75vw;
    margin-right: 0.75vw;
}
img{
    color: white;
}
.logo{
    color: var(--clr-header) ;
}
.LogoA{
    order: 0;
}
 .bars{
     display: none;
     color: var(--clr-heading);
 }
.logoText{
    max-width: 96px;
    font-family: Montserrat,sans-serif;
    margin: 2vh auto 0;
    font-weight: bold;
    font-size: 18px;
    text-align: center;
    text-transform: uppercase;
    color: var(--clr-header);
    text-decoration: none;
    display: flex;
    flex-direction: column;
}
.active
{
    font-family: Arial,sans-serif;
    font-weight: 750;
    padding: 0.1em 0;
    border-bottom: 2px solid black;
    font-size: 14px;
    fill:var(--clr-header);
    cursor: pointer;
    text-transform: uppercase;
    color: var(--clr-header);
}
.linkA{
    font-family: Arial,sans-serif;
    /*font-weight: 400;*/
    padding: 0.1em 0;
    border-bottom: 2px solid transparent;
    font-size: 14px;
    fill: var(--clr-header);
    color:var(--clr-header);
    cursor: pointer;
    text-transform: uppercase;
}
@media (max-width: 414px){
    .bars{
        display: block;
        position: fixed;
        left: 15px;
        top: 15px;
    }
    .header{
        height: 100vh;
        width: 100%;
        background: var(--clr-body-bg);
        position: fixed;
        display: grid;
        grid-template-rows: 25% 30% 30%;
        /*gap: 25px;*/
        left: -100%;
        flex-direction: column;
        z-index: 12;
        border-right: 1px solid var(--clr-text);
        transition: left 0.7s
    }
    .activeHeader{
        left: 0;
    }
    .navLeft,
    .navRight{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        padding: 0 0 0 0;
    }
    .navRight{
        justify-content: center;
        gap: 30px;
    }
    .LogoA{
        order: -1;
        display: flex;
        flex-direction: column;
        align-items: center;

    }
    .divA{
        margin: 15px 0 15px 0;

    }
}