.img{
    border-radius: 20px;
    width: 200px;
    height: 250px;
}

.productBox{
    cursor: pointer;
    display: flex;
    min-height: 300px;
    border-radius: 20px;
    background: white;
    margin: 5px 20px 40px 0;
    padding: 5px 0 5px 30px;
    flex-direction: column;
}

.title{
    margin: 13px 0 0;
    color: #545557;
    text-transform: capitalize;
    font-size: 16px;
    font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
    font-weight: 700;
}
.price{

    font-size: 14px;
    margin: 13px 0 0;
    text-align: left;
    color: #7b7c80;
    font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
}
.description{
    margin: 13px 0 0;
}
.storeBox{
    border-top: 1px solid #bababa;
    border-bottom: 1px solid #bababa;
    background: transparent;
    padding: 15px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
@media (max-width: 992px){
    .productBox{
        align-items: center;

    }
}
    @media (max-width: 414px){
    .productBox:nth-child(3){
        display: none;
    } .productBox:nth-child(4){
          display: none;
      }
    .productBox{
        margin: 0 0 0 0;
    padding: 0 0 0 0;
    }
}
