
.h2 {
    margin: 0;
    padding: 10px 0;
    height: 20px;
    width: 100%;
    font-variant: small-caps;
}
.socials{
    padding: 5px 0;
    flex-wrap: wrap;
    width: 12%;
    margin: auto;
    display: flex;
    justify-content: space-between;
}
.a {
    font-size: 16px;
    margin: 10px 0;
    height: 30px;
    font-weight: bold;
    text-decoration: none;
    color: white;
    position: relative;
    font-variant: small-caps;
    top: 1px;
}
.instagram {
    color: #FFA9C7;
    font-size:30px;
    top: 1px;
    /*background-image: linear-gradient(to left bottom, rgb(135, 0, 184), rgb(231, 6, 209), yellow);*/
}
.telegram {
    color:#249CD8;
    font-size: 30px;
}
.github{color: snow;
    font-size: 30px;
}
.twitter{
    color: #249CD8;
    font-size: 30px;
}
.facebook{
    color: steelblue;
    font-size: 30px;
}
.instagram:hover, .telegram:hover,
.github:hover,.facebook:hover,.twitter:hover {
    position: relative;
    top: 0;

}
.a:hover {
    text-decoration: underline;
    position: relative;
    top: 0;
    color: white;
}
.h3 {
    font-weight: bold;
    width: 100%;
    height: 20px;
    margin: 10px 0;
    padding: 5px 0;
   font-size: 18px;
}
.h4{
    padding: 3px 0;
    margin: 0;
    width: 100%;
}
.div3 {
    font-weight: bold;
    color: white;
    text-align: center;
    width: calc(100% - 16px);
    padding: 10px 8px;
}
.paymentOption{
    height: 40px;
    margin: 10px 0 50px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    text-align: center;
}

li{
    height: 40px;
    margin: 0 4px;
    opacity: .33;
    background: url(//cdn.shopify.com/s/files/1/0286/7706/t/3/assets/main-sprite.png?7644007836053788700) -18px -18px #ffffff;
}
.interac {
    width: 31px;
    background-position: -185px -218px;
}
.visa{
    width: 46px;
    background-position: -230px -218px;
}
.mastercard {
    width: 38px;
    background-position: -291px -218px;
}
.paypal {
    width: 53px;
    background-position: -673px -218px;
}
@media (max-width: 992px){
    .socials{ width: 40%;}
}
@media (max-width: 414px){
    .paymentOption{
        margin: 110px 0 50px;
    }
    .socials{
        width: 70%;
    }
}