.titleAbout{
    margin: 0 0 10px 0;
    font-size: 35px;
    font-weight: 700;
    font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
    color: var(--clr-heading);
}
.textAbout{
    color: var(--clr-aboutText);
    width: 70%;
    font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
    font-size: 14px;
    line-height: 22px;
}
.blockAbout{
    display:flex;
    flex-direction: column;
    margin: 100px 0;
    padding: 25px 0;
    border-top: 1px solid #bababa;
    border-bottom: 1px solid #bababa;
}
@media (max-width: 992px){
.blockAbout{
    align-items: center;
}

}