.color-picker{
   & > fieldset {
     position: absolute;
     top: 4.5rem;
     right: 2rem;
    border: 0;
    display: flex;
    gap: 1.5rem;
    padding: 1rem 3rem;
    margin: 0 auto 1rem;
    width: fit-content;
  }
}

.color-picker input[type='radio']{
  appearance: none;
  width: 1rem;
  height: 1rem;
  cursor: pointer;
  outline: 3px solid var(--radio-color, currentColor);
  outline-offset: 3px;
  border-radius: 20%;
  &:hover{
    opacity: 0.7;
    outline: 3px solid var(--radio-color, currentColor);
    outline-offset: 3px;
    background-color: var(--radio-color);
  }
}
.color-picker input[type='radio']:checked{
  background-color: var(--radio-color);
}
.color-picker input[type='radio']#pink{
  --radio-color: #ffa5b4;
}
.color-picker input[type='radio']#light{
  --radio-color: #b4b4b4;
}

.color-picker input[type='radio']#blue{
  --radio-color: #8f8ff6;
}
.color-picker input[type='radio']#green{
  --radio-color: #7fd07f;
}
.color-picker input[type='radio']#dark{
  --radio-color: #232323;
}

:root:has(#pink:checked){
  --clr-body-bg:hsl(310 50% 90%);
  --clr-card-bg:hsl(310 50% 100%);
  --clr-text:hsl(310, 50%, 15%);
  --clr-heading: hsl(310, 50%, 25%);
  --clr-btnHover: hsl(310, 50%, 45%);
  --clr-inputBorder: hsl(310 50% 10%)
}
:root:has(#blue:checked){
  --clr-body-bg:hsl(209 50% 90%);
  --clr-card-bg:hsl(209 50% 100%);
  --clr-text:hsl(209, 50%, 15%);
  --clr-heading: hsl(209, 50%, 25%);
  --clr-btnHover: hsl(228,  50%, 45%);
  --clr-inputBorder: hsl(209 50% 25%)
}
:root:has(#green:checked){
  --clr-body-bg:hsl(109 50% 90%);
  --clr-card-bg:hsl(109 50% 100%);
  --clr-text:hsl(109, 50%, 15%);
  --clr-heading: hsl(109, 50%, 25%);
  --clr-btnHover: hsl(109, 50%, 45%);
  --clr-inputBorder: hsl(113, 47%, 29%)
}
:root:has(#dark:checked){
  --clr-body-bg: hsl(0, 0%, 6%);
  --clr-card-bg:hsl(209 50% 5%);
  --clr-text:hsl(209, 50%, 90%);
  --clr-heading: hsl(0, 100%, 100%);
  --clr-aboutText: hsl(0, 0%, 100%);
  --clr-btnHover: hsl(0, 2%, 100%);
  --clr-header: hsl(0, 0%, 80%);
  --clr-inputBorder: hsl(0 50% 100%)
}
:root:has(#light:checked){
  --clr-body-bg:hsl(0 0% 100%);
  --clr-card-bg:hsl(0 0% 100%);
  --clr-text:hsl(228, 2%, 49%);
  --clr-heading: hsl(220, 2%, 34%);
  --clr-aboutText: hsl(228, 2%, 49%);
  --clr-btnHover: hsl(0, 0%, 0%);
  --clr-header: hsl(0, 0%, 23%);
  --clr-inputBorder: hsl(0 0% 0%)
}
@media (max-width: 992px) {
  .color-picker{
    & > fieldset {
      position: static;
      margin: 0 auto 1rem;
      justify-content: center;
      right: 0;
    }
  }
}
@media (max-width: 414px) {
  .color-picker{
    & > fieldset {
      position: static;
    }
  }
}