.mainContainer{
    z-index: 1;
    margin: 45px 0;
    height: 75vh;
    width: 100%;
    display: flex;
    align-items: center;
}
.window{
    height: 100%;
    width: 100%;
    overflow: hidden;
}
.allPagesContainer{
    height: 100%;
    display: flex;
    transition: translate;
    transition-property: transform;
    transition-duration: 500ms;
    transition-timing-function: ease-in-out;

}
.arrowLeft:hover,.arrowRight:hover{
    opacity: 0.7;
}
.arrowLeft{
    padding: 15px 0;
    z-index: 2;
    left: 0;
    position: absolute;
    font-size: 50px;
    background: white;
    opacity: .5;
    cursor: pointer;
}
.arrowRight{
    padding: 15px 0;
    z-index: 2;
    right: 0;
    position: absolute;
    font-size: 50px;
    background: white;
    opacity: .5;
    cursor: pointer;
}
.item1Content{
    font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
    margin: 0 15vh 0 0;
}

.title{
    user-select: all;
    line-height: 1;
    font-size: 48px;
    font-weight: 600;
    cursor: default;

    text-shadow: none;
    color: #fff;
}
.text{
    width: 50%;
    user-select: all;
    font-size: 17px;
    font-weight: 400;
    margin-top: 4px;
    cursor: default;
    color: #fff;
}
.button{
    padding: 20px;
    line-height: 1em;
    font-size: 15px;
    font-weight: 700;
    letter-spacing: .1em;
    color: #fff;
    background-color: #000;
    display: inline-block;
    vertical-align: middle;
    zoom: 1;
    text-transform: uppercase;
    margin-top: 24px;
}
.button:hover {
    opacity: 0.8
}

.img{
   position: relative;
    width: 250px;
    height: 250px;
}
.btnArrow{
    margin: 0 0 0 5px;
    position: relative;
    top: 2px;
}
@media (max-width: 992px){
    .arrowRight, .arrowLeft{
        font-size: 50px;
        padding: 5px 0;
    }
    .item1Content{
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        margin: 0 0 0 0;
    }
}