.gallery{
  margin: 15vh 0 0 0;
  min-width: 100%;
}
.firstBlock{
  display: flex;
  justify-content: space-between;
  font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
  > .text{
    max-width: 50%;
    color: var(--clr-text);
    display: flex;
    flex-direction: column;
    justify-content: center;
    //align-items: center;
    text-align: center;
    padding: 0 15px 0 15px;
  }
  > div > h2{
    font-weight: bold;
    font-size: 24px;
    padding: 25px 0;
  }
  div > p {
    margin: 15px 0;
    font-size: 18px;
  }
  border-bottom: 1px solid #bababa;
  border-top: 1px solid #bababa;
  .imgDiv{
    max-width: 60%;
   padding: 0 0 0 0;
   height: 90vh;
    img{
      width: 100%;
      height: 100%;
    }
  }
}

@media (max-width: 1400px) {
  .firstBlock{
    .imgDiv{
      height: 100vh;
    }
  }
}
@media (max-width: 1200px) {
  .gallery{
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  .firstBlock{

    flex-wrap: wrap;
    flex-direction: column-reverse;

    .text{
      min-width: 100% ;
      padding: 20px;
    }
    .imgDiv{
      min-width: 100% ;
      width: 100%;
      img{
        width: 100%;
      }
    }
  }
}

@media (max-width: 992px) {
  .firstBlock{
    flex-wrap: wrap;
    .imgDiv{
      height: 75vh;
      width: 100%;
    }
  }
}
@media (max-width: 767px) {
  .firstBlock{
    > div > h2{
      font-size: 20px;
      padding: 25px 10px;
    }
    div > p {
      margin: 15px 25px;
      font-size: 16px;
    }
  }
}
@media(max-width: 414px){
  .firstBlock{
    .imgDiv{
      width: 50%;
      height: 60%;
  }

    img{
      width: 100%;
      height: 40%;
    }
  }
}