*{padding: 0;margin: 0;border: 0;}
*,*:before,*:after{-moz-box-sizing: border-box;-webkit-box-sizing: border-box;box-sizing: border-box;}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}
nav,footer,header,aside{display: block;}
html,body{height:100%;width:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%;}
input,button,textarea{font-family:inherit;}
input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner{padding:0;border:0;}
a,a:visited{text-decoration: none;}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}
h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight: inherit;}

html,body,#root,.App{
    height: 100%;
}
.App, .wrapper{
    background-color: var(--clr-body-bg);
}
.wrapper {
    min-height: 100%;
    display: flex;
    flex-direction: column;

}
.Container {
    width: 90%;
    max-width: 1180px;
    margin: 0 auto;

}
.MainWrapper{ flex: 1 1 auto;}
footer{
    padding-top: 20px;
    height: 100%;
    width: 100%;
    background: rgba(0,0,0,.9);
}
.body {
    background: var(--clr-body-bg);
    color: var(--clr-text);
    font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
}
.themes{
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
    padding: 20px 0;
div{
    cursor: pointer;
    width: 20px;
    height: 20px;
    border-radius: 4px;
    margin: 2px 10px;
    border: 1px solid #fff;
    display: inline-block;
    opacity: 0.4;
&.active{
     opacity: 1;
 }
&:hover{
     opacity: 0.7;
 }
}

}
.theme-default{
    background-color: white;
}
.theme-red{
    background-color: #dadab2;
}
.theme-blue{
    background-color: #b1ccff;
}
.theme-green{
    background-color: #b7dab7;
}
.theme-purple{
    background-color: #fdcea1;
}
:root{
    --clr-header: hsl(0, 0%, 23%);
    --clr-body-bg:hsl(0 0% 100%);
    --clr-card-bg:hsl(0 0% 100%);
    --clr-text:hsl(228, 2%, 49%);
    --clr-heading: hsl(220, 2%, 34%);
    --clr-aboutText: hsl(228, 2%, 49%);
    --clr-btnHover: hsl(228, 2%, 49%);
    --clr-inputBorder: hsl(0 0% 0%)
}
.visually-hidden{
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
}
@media (max-width: 414px) {
    .Container{
        width: 100%;
        margin: 0;
    }}
