.overlay{
  z-index:100;
  background-color: rgba(0,0,0,0.6);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.modalContainer{
  border: 2px solid var(--clr-inputBorder);
  padding: 1rem;
  background-color: white;
  max-width: 600px;
  width: 100%;
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  box-shadow: 0 0 18px 0 rgba(0,0,0, 0.75);

}
.closeModuleBtn{
  position: fixed;
  top: 8px;
  right: 8px;
  cursor: pointer;
  margin: 0 0;
  &:hover{
    opacity: 0.7;
  }
}
.modalRight{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 20px;
  width: calc(600px - 270px)
}
.modalLeft{
  width: 250px;
  &:after{
    content: '';
    display: block;
    padding: 0.5px;
    transform: rotate(0.25turn) translate(-50%, -130px);
    background-color: var(--clr-inputBorder);
  }
}
.description{
  text-align: center;

}
.tittle{
  text-align: center;
  text-transform: uppercase;
  font-size: 30px;
  margin: 10px 0;
}
.price{
  text-align: center;
  color: grey;
  font-weight: bold;
}
.buttonContainer{
  display: flex;
  button {
    width: 100%;
    margin: .5rem;
    padding: 16px 0;
    border-radius: 15px;
    border: 2px solid var(--clr-inputBorder);
    background-color: var(--clr-body-bg);
    color: var(--clr-text);
    &:hover{
    background-color: var(--clr-btnHover);
      color: var(--clr-body-bg);
      border: 2px solid var(--clr-body-bg);
  }
  }
}
.img{
  border-radius: 20px;
  width: 250px;
  height: 250px;

}
@media (max-width: 414px){
  .modalContainer{
    height: 600px;
    top: 50%;
    flex-wrap: wrap;
  }
  .closeModuleBtn{
    font-size: 40px;
  }
  .modalLeft{
    width: 100%;
    &:after{
      position: relative;
      top: 25px;
      transform: rotate(0.5turn) translate(0, 0);
    }
  }
  .img{
    width: 100%;
    height: 300px;
  }
  .modalRight{
    width: 100%;
  }
}