.myLabel{
    margin: 10px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}
.myInput{
    border-radius: 3vh;
    padding: 8px;
    width: 200px;
    height: 30px;
    border: 2px solid var(--clr-inputBorder);
  background-color: transparent;
  color: var(--clr-inputBorder);
  font-weight: bolder;
}
.myInput:focus{
    border: 2px solid mediumpurple;

}

.form{
  margin: 5rem 0;
    font-family: Montserrat,sans-serif;
    flex-wrap: wrap;
    height: 70vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    span{
         margin: 0 0;
    }
    input{
        margin: 10px 0;
    }
}
.textarea{
  background-color: transparent;
    resize: none;
    font-size: 20px;
  width: 25rem;
  height: 10rem;
    padding: 5px;
    border: 2px solid var(--clr-inputBorder);
}
.errorMsg{
    color: red;
}
.button{
  position: relative;
    font-family: Montserrat,sans-serif;
    font-size: 1.4rem;
  text-transform: uppercase;
   padding: 5px 15px;
    margin-bottom: 15px;
  border-radius: 10px;
  border: 2px solid var(--clr-inputBorder);
   height: 3rem;
   width: 10rem;
   overflow: hidden;
   transition: .5s;
   position: relative;
   display: inline-block;
   background-color: transparent;
   color: var(--clr-inputBorder);
}
.button:hover {
   background: var(--clr-btnHover);
   border: 0;
   color: var(--clr-body-bg);
   box-shadow:
   0 0 5px var(--clr-btnHover),
   0 0 25px var(--clr-btnHover)
}




